<template>
	<!-- <swiper ref="mySwiper" :options="swiperOptions">
    <swiper-slide v-for="(item,index) in swpieItems" :key="index">
      <img :src="`https://ccc.wangwanglive.com${item.pic}`">
    </swiper-slide>
    
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper> -->

	<view>
		<view class="uni-padding-wrap">
			<view class="page-section swiper">
				<view class="page-section-spacing">
					<swiper class="swiper" indicator-color="rgba(0,0,0,.2)" indicator-active-color="#fff" :indicator-dots="indicatorDots"
					 :autoplay="autoplay" :interval="interval" :duration="duration">
						<swiper-item v-for="(item,index) in swpieItems" :key="index">
							<view class="swiper-item ">
								<image :src="item.pic" mode="aspectFilt" @click="tao(item.url, item.appid)"></image>

 
								<!-- 无法读取 -->
								<!-- 1/：src为正确格式，其余不行，不要使用{{}} -->
								<!-- 2、数据格式如果为JSON，请把poputy  双引号上 -->


								<!-- {{item}} -->
							</view>
						</swiper-item>

					</swiper>
				</view>
			</view>
		</view>


	</view>
</template>

<script>
	export default {
		name: 'carousel',
		props: {
			swpieItems: Array
		},
		data() {
			return {
				background: ['color1', 'color2', 'color3'],
				indicatorDots: true,
				autoplay: true,
				interval: 2000,
				duration: 500
			}
		},
		methods: {
			 

			// 判断是否跳转其他小程序，没有的话跳转小程序内部页面
			// 判断是否为
			tao(url, id) {
			console.log(id)
			   /*  #ifdef  MP-ALIPAY  */
			   if(id==''){
			     my.ap.navigateToAlipayPage({
			    path: url,
			    appParams: {},
			    success:(res) => {
			      //  my.alert({content:'成功：'+JSON.stringify(res)});
			    },
			    fail:(res) => {
			        my.alert({content:'失败：'+JSON.stringify(res)});
			    } 
			}); 
				return ;
			}
		
				   /*  #endif  */
				
				if (!id) {
					uni.navigateTo({
						url: url 
					})
				} else {
					uni.navigateToMiniProgram({
						appId: id,
						path: url,
						success: res => {
							// 打开成功
							console.log("打开成功", res);
						},
						fail: err => {
							console.log(err);
						}
					});

				}


			}

		}
	}
</script>
<style lang="less">
	uni-swiper {
		height: 160px;
	}
	swiper{
		height: 160px;
	}
	

	.swiper-item {
		height: 160px;

		image {
			width: 100%;
			height: 160px;
			background-size: 100% 160px !important;
			
		}
	}

	/* #ifdef H5 */


	@media screen and (min-width: 750px) {
		uni-swiper {
			height: 320px;
		}

		.swiper-item {
			height: 320px !important;

		}

		.swiper-item image {
			width: 100%;
			height: 320px !important;
		}
	}


	/* #endif */
</style>
